<template>
  <div>
    <Nav />
    <div class="lg:container mx-auto">
      <div class="md:container mx-auto quiz-container px-8">
        <div class="quiz-header py-4 bg-gray-200">
          <h1 class="text-xl font-semibold text-center">Quizz</h1>
        </div>
        <h2 class="text-lg text-center font-bold text-gray-500">
          Question {{ curQ }}/{{ questions.length }}
        </h2>

        <main
          class="quiz-main"
          v-for="(q, idx) in questions.slice(filter, curQ)"
          :key="idx"
        >
          <p>{{ q.question }}</p>

          <ul class="options">
            <li
              v-for="(op, index) in q.options"
              :key="index"
              :class="selected ? checkAns(q.correct_answer, op) : ''"
              @click="selectOpt(q.correct_answer, op)"
            >
              {{ op }}
            </li>
          </ul>
        </main>

        <footer class="quiz-footer py-3 bg-gray-200 mt-10">
          <button v-if="filter != curQ"
            class="rounded bg-gray-100 p-2 hover:bg-gray-500 font-semibold border"
            @click="next"
          >
            {{questions.length != (curQ)? 'Next' : 'Finish'}}
          </button>
        </footer>

        <div class="score" v-if="filter == curQ">
          <h1 class="font-semibold text-xl text-center">Your Score:</h1>
<span class="text-4xl font-semibold text-center block"> {{success}} of {{questions.length}} </span>
      <button
            class="rounded bg-gray-100 p-2 hover:bg-gray-500 font-semibold border"
            @click="restart"
          >Restart</button>
        </div>
        
      </div>
    </div>
  </div>
</template>

<script>
import Nav from "../components/Nav";
import Questions from "../questions.js";

export default {
  mounted() {
    this.formatQuestions();
  },
  components: {
    Nav,
  },

  data() {
    return {
      filter: 0,
      curQ: 1,
      selected: false,
      success: 0,
      questions: [],
    };
  },

  methods: {
    shuffle(array) {
      for (let i = array.length - 1; i > 0; i--) {
        let j = Math.floor(Math.random() * (i + 1));
        [array[i], array[j]] = [array[j], array[i]];
        return array;
      }
    },

    formatQuestions() {
      for (let index = 0; index < Questions.length; index++) {
        const question = Questions[index];
        question.options = this.shuffle([
          ...question.incorrect_answers,
          question.correct_answer,
        ]);
        this.questions.push(question);
      }
    },
    selectOpt(corOpt, opt) {
     if (this.selected) {
       return
     } else {
       this.selected =true
       corOpt == opt?this.success++:console.log('wrong answer');
     }
    },
    checkAns(corOpt, opt) {
      if (corOpt == opt) {
        return "correct";
      } else {
        return "incorrect";
      }
    },
    next() {
      this.selected = false;
      this.curQ != this.questions.length ? this.curQ++ : '';
      this.filter != this.curQ? this.filter++: ''
    },
    restart() {
      this.selected = false;
      this.curQ =1
      this.filter =
      this.success = 0
    },
  },
};
</script>

<style scoped>
li {
  list-style: none;
  margin: 0px 0;
  padding: 10px 5px;
  transition: all 0.3s;
  cursor: pointer;
}

li:nth-child(odd) {
  background: rgba(204, 204, 204, 0.219);
}
li:hover {
  background: rgba(0, 0, 0, 0.13);
}
ul {
  border: 1px solid #ccc;
  margin: 10px 0;
}

.correct {
  background: rgb(61, 238, 61) !important;
  color: white;
}
.incorrect {
  background: rgb(235, 76, 76) !important;
  color: white;
}
</style>
